*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* body{
  background-color: rgb(30 35 41);
} */

#root{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content{
  flex: 1;
}
.filters{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  box-shadow: 1px 1px 3px grey;
  flex-wrap: wrap;
}

.analytics-icon *{
  margin: auto 0.3rem;
}
.active-icon{
  color: blue;
}