.login-page , .register-page{
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    /* background-color: #333333; */
    background-color: #1a1a1a;
}
.login-form,.register-form{
    background-color: white;
    padding: 4rem;
    border-radius: 1.3rem;
    font-size: 1rem;
    font-weight: 700;
}
.buttons{
    display: flex;
    gap:2rem;
    flex-wrap: wrap;
    font-weight: normal;
}